.notifications {
  padding: 20px;
  background-color: #f5f5f5;
  min-height: 100vh; /* Assure que la section occupe toute la hauteur de la vue */
  width: 100%;
}

.notifications ul {
  list-style-type: none; /* Suppression des puces */
  padding: 0;
  margin: 0;
}

.notifications li {
  display: flex;
  align-items: center;
  background-color: #ffffff;
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  margin-bottom: 10px;
  padding: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s, box-shadow 0.3s;
  margin: 0;
}

.notifications li:hover {
  background-color: #f0f8ff; /* Couleur de survol */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.notifications img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 15px;
}

.notifications .notif-content {
  flex-grow: 1;
}

.notifications .follow {
  color: #007bff; /* Couleur pour les demandes d'amis */
}

.notifications .like {
  color: #28a745; /* Couleur pour les mentions J'aime */
}

.notifications .comment {
  color: #dc3545; /* Couleur pour les commentaires */
}

.notifications p {
  text-align: center;
  color: #888;
  font-size: 16px;
  margin-top: 20px;
}
