
.profile {
    display: flex;
    flex-direction: column; /* Vertical stacking */
    align-items: center; /* Horizontal centering */
    padding: 20px;
    font-family: Arial, sans-serif;
    width: 100%;
    box-sizing: border-box;
    margin: 0; /* Ensure it's flush with the top */
    
}

/* Profile Picture Styling */
.profile-pic {
    width: 150px;
    height: 150px;
    border-radius: 50%; /* Makes the image round */
    border: 2px solid #dbdbdb;
    object-fit: cover;
    transition: transform 0.3s ease-in-out;
    margin: 0; /* Center the image horizontally */
}

/* Hover effect for the profile picture */
.profile-pic:hover {
    transform: scale(1.05); /* Slightly enlarges the image on hover */
}

.profile h3 {
    margin-top: 20px; /* Adds space between the image and the username */
}

/* Profile Info section */
.profile-info {
    display: flex;
    flex-direction: row;
    column-gap: 30px;
    justify-content: space-evenly;
    width: 100%;
    max-width: 500px;
    margin-top: 20px;
    border-top: 1px solid #dbdbdb;
    padding-top: 10px;
    background-color: #f7f7f7;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Styling for h3 inside .profile-info */
.profile-info h3 {
    font-size: 16px;
    font-weight: 400;
    color: #8e8e8e;
}

/* Special styling for the first child in .profile-info */
.profile-info h3:first-child {
    color: #000;
}

/* Additional margin for the second h3 */
.profile-info h3:nth-child(2) {
    margin-left: 20px;
}

/* Button styling */
.profile-btn {
    background-color: #4CAF50;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 20px;
}

/* Button hover effect */
.profile-btn:hover {
    background-color: #3e8e41;
}

/* Social links container */
.profile-social {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 20px;
}

/* Styling for social links */
.profile-social a {
    text-decoration: none;
    color: #337ab7;
}

/* Hover effect for social links */
.profile-social a:hover {
    color: #23527c;
}
