.chat {
    display: flex;
    flex-direction: column;
    height: 100vh;
    padding: 20px;
    width: 100%;
    background-color: #f4f4f4; /* Lighter background for the chat */
    border: 1px solid #ddd; /* Subtle border color */
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    box-sizing: border-box;
  }
  
  .messages {
    flex: 1;
    overflow-y: auto;
    padding: 20px;
    display: flex;
    flex-direction: column;
    background-color: #fff; /* White background for the message area */
    border-radius: 10px;
  }
  
  .messages-list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
  }
  
  .message {
    margin-bottom: 15px;
    padding: 12px;
    border-radius: 12px; /* Slightly more rounded corners */
    max-width: 80%; /* Adjust the width as needed */
    word-wrap: break-word; /* Ensure long words or URLs break properly */
  }
  
  .bubble {
    --r: 1em;  /* the radius */
    --t: 1.5em; /* the size of the ætail */
    
    max-width: 300px;
    padding: 1em;
    border-inline: var(--t) solid #0000;
    border-radius: calc(var(--r) + var(--t))/var(--r);
    mask: 
      radial-gradient(100% 100% at var(--_p) 0,#0000 99%,#000 102%) 
        var(--_p) 100%/var(--t) var(--t) no-repeat,
      linear-gradient(#000 0 0) padding-box;
    background: linear-gradient(135deg,#FE6D00,#1384C5) border-box;
    color: #fff;
  }
  .left {
    --_p: 0;
    border-bottom-left-radius: 0 0;
    place-self: start;
  }
  .right {
    --_p: 100%;
    border-bottom-right-radius: 0 0;
    place-self: end;
  }
  
  body {
    margin: 0;
    min-height: 100vh;
    display: grid;
    place-content: center;
    gap: 20px;
    font-family: system-ui, sans-serif;
    font-size: 20px;
  }
  
  .message-content {
    font-size: 16px;
  }
  
  .message-time {
    font-size: 12px;
    color: #999; /* Lighter grey for timestamps */
  }
  
  .input {
    padding: 10px;
    background-color: #fff; /* White background for the input area */
    border-top: 1px solid #ddd; /* Subtle border color */
  }
  
  input[type="text"] {
    width: calc(100% - 22px); /* Adjust width to account for padding and borders */
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc; /* Light grey border */
    border-radius: 5px; /* Rounded corners */
    box-sizing: border-box;
  }
  
  button[type="submit"] {
    background-color: #007bff; /* Blue background for button */
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-left: 10px; /* Add margin to separate from input field */
  }
  
  button[type="submit"]:hover {
    background-color: #0056b3; /* Darker blue on hover */
  }
  