/* Container for the friends carousel */
.friends {
    display: flex;
    flex-direction: row;
    overflow-x: auto;
    scroll-behavior: smooth;
    padding: 10px;
    gap: 20px;
    width: 100%;
    white-space: nowrap;
    background-color: #f9f9f9;
    border-radius: 15px;
}

.friends::-webkit-scrollbar {
    height: 8px;
}

.friends::-webkit-scrollbar-thumb {
    background-color: #888;
    border-radius: 10px;
}

.friends::-webkit-scrollbar-thumb:hover {
    background-color: #555;
}

.friends::-webkit-scrollbar-track {
    background-color: #f1f1f1;
}
  
/* Individual friend card */
  .friend {
flex: 0 0 120px;
    height: 150px;
    background-color: #fff;
    text-align: center;
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    transition: transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  }
  
  .friend:hover {
    transform: scale(1.05);
    box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.2);
  }
  
  /* Friend profile picture */
.friend img {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    object-fit: cover;
    }

/* Friend's full name */
.friend span {
    font-size: 14px;
    color: #333;
    font-weight: bold;
    margin-top: 10px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    width: 100%;
    text-align: center;
}

@media (max-width: 600px) {
    .friend {
        flex: 0 0 100px;
        height: 130px;
    }

    .friend span {
        font-size: 12px;
    }
  }
  
  .friend:focus {
    outline: 2px solid #007bff;
}

h3 {
    font-family: 'Montserrat', sans-serif;
    color: #333;
    margin-bottom: 20px;
    font-size: 20px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;
  }
  